@use "base/variables" as v;
@use "base/mixins" as m;

.descargar {
    background-color: v.$grisClaro;
    padding: v.$separacion;

    &__grid {
        @include m.tablet { 
            @include m.grid(2, v.$separacion);
        }        
    }
    &__imagen {
        width: 50%;
        margin: 3rem auto 0 auto;
        transition: transform 0.3s;

        @include m.tablet { 
            margin-top: 0;
        }
        &:hover {
            transform: rotate(4deg) scale(1.2);
        }
    }
}